.card-header {
  border: none;
  color: rgb(251, 250, 252);
  background: rgb(247, 119, 230);
  background: linear-gradient(
    90deg,
    rgb(247, 119, 230) 33%,
    rgba(252, 176, 69, 1) 100%
  );
}
.card {
  text-align: center;
  margin: auto;
  border: none;
  width: 85%;
}

.sear {
  background: -webkit-linear-gradient(rgb(250, 63, 250), rgb(240, 148, 220));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.searchBar {
  margin-top: 50px;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}
.searchBtn {
  border: none;
  color: rgb(238, 67, 215);
  font-size: 20pt;
  background: transparent;
}

.searchBtn:hover {
  color: rgb(245, 154, 69);
}

.list-item p {
  font-size: 16px;
}

.btn-container {
  float: right;
}

@media (max-width: 767px) {
  .btn-container {
    margin-bottom: 15px;
  }
}

.imgThumbnail {
  height: 300px;
  margin-left: 30px;
}

.authors {
  font-style: italic;
  color: rgb(53, 34, 0);
}

.bookTitle {
  color: rgb(226, 147, 1);
}

.subTitle {
  font-style: italic;
  color: rgb(250, 181, 53);
}

.description {
  float: left;
}

.cont {
  font-family: "Nunito Sans", sans-serif;
  color: rgb(124, 122, 122);
  height: 70vh;
  bottom: 0;
}

.navbar {
  height: auto;
  background-color: rgb(247, 119, 230);
}

.navItem {
  padding: 20px;
}

.logo {
  height: 80px;
}

.footer {
  font-size: 10pt;
  text-align: center;
  height: 100px;
  padding: 10px;
  margin: 0px;
}

hr {
  margin: 0;
}

.wrapper {
  min-height: 100%;
  background-color: rgb(243, 184, 243);
}

